.program__list {
  width: 320px;
  padding: 0 20px;

  .program__item {
    display: flex;
    flex-direction: column;
    width: 280px;
    background-color: #d9d9d9;
    }
  }

  .program__title {
    margin-bottom: 29px;
  }
  .program__description {
    font-family: "Lato", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #444444;
    margin: 0 auto 38px;
    padding: 0 20px;
  }

.program__catalog {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
  //margin: 0 0 20px;
  padding: 0 20px;
}

